/**
 * debounce
 * @param func
 * @param wait
 * @param immediate
 * @return {Function}
 */
export function debounce(func, wait, immediate = false) {
  // immediatefalse
  let timeout, args, context, timestamp, result;
  let later = function () {
    // waitdebouncetimestamplast < wait && last >= 0truefunc
    let last = new Date().valueOf() - timestamp;
    if (last < wait && last >= 0) {
      timeout = setTimeout(later, wait - last)
    } else {
      timeout = null;
      if (!immediate) {
        result = func.apply(context, args);
        if (!timeout) {
          context = args = null;
        }
      }
    }
  };
  return function () {
    context = this;
    args = arguments;
    timestamp = new Date().valueOf();
    // immediatetruefunc
    let callNow = immediate && !timeout;
    // waitfunc
    if (!timeout) {
      timeout = setTimeout(later, wait)
    }
    if (callNow) {
      result = func.apply(context, args);
      context = args = null;
    }
    return result
  }
}

export default debounce


